div.about-heading {
	--h2-width: 0;
	position: relative;
	margin-bottom: 2.5rem;
}

div.about-heading.left > div {
	justify-content: flex-start;
}

div.about-heading.right > div {
	justify-content: flex-end;
}


div.about-heading h2 {
	font-style: italic;
	letter-spacing: 15%;
	font-weight: 800;
	padding: 0.3em 0;
	transform: scaleX(0.9);
	position: relative;
	z-index: 1;
}


div.about-heading.left h2 {
	transform-origin: left;
}

div.about-heading.right h2 {
	transform-origin: right;
}

div.about-heading div.background {
	height: 100%;
	width: calc((100% - var(--max-width-small)) / 2 + var(--h2-width) + 4rem);
	min-width: calc(var(--side-padding) + var(--h2-width) + 4rem);
	position: absolute;
	top: 0;
}

div.about-heading.left div.background {
	clip-path: polygon(0px 0px, 100% 0px, calc(100% - 1.5rem) 100%, 0px 100%);
	left: 0;
    background-color: var(--light-blue);
	opacity: 50%;
}

div.about-heading.right div.background {
	clip-path: polygon(0px 0px, 100% 0px, 100% 100%, 1.5rem 100%);
	right: 0;
    background-color: var(--dark-blue);
	opacity: 85%;
}

div.about-heading.right h2 {
	color: var(--faded-blue);
}

